.footer__copyright{
    display: flex;
    align-items: center;

    p{
        color: rgba($color: #ffffff, $alpha: 0.65);
        font-family: $MS-Regular;
    }

    .copyright__svg{
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.footer{
    background: linear-gradient(180deg, rgba(40, 40, 40, 0.28) 0%, rgba(40, 40, 40, 0.1036) 100%);
    backdrop-filter: blur(14px);
}

.footer__container{

    .footer__wrapper{
        width: 100%;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: vmin(1);
            background-color: rgba($color: #fff, $alpha: .65);
        }
    }
}

.footer__wrapper{
    display: flex;

    .footer__social{
        display: flex;
        align-items: center;
        
        a.footer__social__item{
            margin-left: vmin(30);
            width: vmin(40);
            height: vmin(40);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &:first-child{
                margin-left: 0;
            }
        }
    }

    a.footer__politics{
        color: rgba($color: #ffffff, $alpha: 0.65);
        font-family: $MS-Regular;
    }
}

@media (max-width: 990px){
    .footer{
        padding: vmin(40) 0;
    }

    .footer__copyright{
        margin: 0 auto;
        padding-top: vmin(13);

        p{
            margin-left: vmin(10);
            font-size: vmin(11);
        }
    
        .copyright__svg{
            width: vmin(20);
            height: vmin(20);
        }
    }

    .footer__wrapper{
        flex-direction: column;
        align-items: center;
        padding-bottom: vmin(15);

        .footer__social{
            padding-bottom: vmin(25);
        }

        a.footer__politics{
            font-size: vmin(11);
        }
    }
}