.header{
    background: linear-gradient(180deg, rgba(40, 40, 40, 0.28) 0%, rgba(40, 40, 40, 0.1036) 100%);
    display: flex;
    align-items: center;
}

.header__container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__contacts{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.header__contacts{
    p.header__number{
        color: #FFFFFF;
        font-family: $DTW-Medium;
        padding: 0;
        margin: 0;
    }

    p.header__schedule{
        color: rgba(#fff, 0.7);
        font-family: $MS-Regular;
        padding: 0;
        margin: 0;
    }
}

.header__logo{
    a{
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.header__menu{
    position: relative;

    .header__nav{
        position: absolute;
        right: 0;
        justify-content: center;
        width: vmin(230);
        height: vmin(313);
        display: flex;
        flex-direction: column;
        background: rgba(255, 255, 255, 0.08);
        box-shadow: 0px 51px 74px rgba(0, 0, 0, 0.25);
        backdrop-filter: blur(52px);
        border-radius: vmin(38);
        padding-left: vmin(60);
        top: calc(100% + vmin(42));

        &::after{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: vmin(38);
            padding: vmin(0.8);
            background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
            linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
        }

        ul.nav__list{
            position: relative;

            &::after{
                content: '';
                position: absolute;
                left: vmin(-25.5);
                top: 0;
                width: vmin(2);
                height: 100%;
                border-radius: 50%;
                background-color: rgba($color: #fff, $alpha: .16);
            }

            li.nav__item{
                padding-bottom: vmin(25.5);
    
                &:last-child{
                    padding-bottom: 0;
                }
    
                a.nav__link{
                    font-family: $MS-Medium;
                    font-size: vmin(18);
                    color: #fff;
                    position: relative;
                    z-index: 5;

                    &::after{
                        content: '';
                        position: absolute;
                        left: vmin(-37.5);
                        top: 50%;
                        transform: translateY(-50%);
                        width: vmin(26.5);
                        height: vmin(8);
                        transform: rotate(-90deg);
                        background: radial-gradient(50% 50% at 50% 50%, rgba(0, 255, 255, 0.94433) 3.12%, #00FFFF 6.77%, rgba(0, 255, 255, 0.814433) 15.62%, rgba(0, 255, 255, 0.34) 25%, rgba(0, 255, 255, 0.1) 50.52%, rgba(0, 255, 255, 0) 97.92%);
                    }
                }
            }
        }
    }
    
    .header__burger{
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        
        span{
            background: #FFFFFF;
            position: relative;

            &::before{
                content: '';
                position: absolute;
                background: #FFFFFF;
            }

            &::after{
                content: '';
                position: absolute;
                background: #FFFFFF;
            }
        }
    }
}

@media (max-width: 768px){
    .header__logo{
        width: vmin(108);
        height: vmin(31);
    }

    .header__container{
        padding-top: vmin(60);
        padding-bottom: vmin(25);
    }

    .header__menu{
    
        .header__burger{
            width: 100%;
            height: vmin(27);
            
            span{
                width: vmin(27);
                height: vmin(2);
                border-radius: vmin(3);
    
                &::before{
                    width: vmin(27);
                    height: vmin(2);
                    top: vmin(-10);
                    border-radius: vmin(3);
                }
    
                &::after{
                    width: vmin(27);
                    height: vmin(2);
                    bottom: vmin(-10);
                    border-radius: vmin(3);
                }
            }
        }
    }

    .header__contacts{
        p.header__number{
            font-size: vmin(15);
            padding-bottom: vmin(5);
        }

        p.header__schedule{
            font-size: vmin(11);
        }
    }
}